.bodyView {
	display: flex;
	margin: 1vh;
	/* background-color: lightgray; */
}

.title {
	display: flex;
	justify-content: space-between;
	font-size: 28px;
}

.guideView {
	margin: 20px 10px;
}

.menuView {
	width: 30%;
	height: 98vh;
	overflow-y: scroll;
	display: inline-block;
}

.menuIcon {
	display: none;
}

.detailView {
	width: 70%;
	height: 98vh;
	overflow-y: scroll;
	padding: 5px;
}

@media only screen and (max-width: 450px) {
	.menuView {
		position: fixed;
		width: 75%;
		min-width: 240px;
		max-width: 350px;
		height: 100%;
		overflow-y: scroll;

		z-index: 5; /* Below sticky header */
		margin: 0;
		top: 0;
		left: -75%;

		background-color: rgba(245, 245, 245);
		color: dimgray;
	}

	.menuIcon {
		border: 1px solid gray;
		border-radius: 4px;
		padding: 4px;
		display: inline-block;
	}

	.detailView {
		width: 100%;
		height: 98vh;
		overflow-y: scroll;
		padding: 5px;
	}
}

.menuOverlay {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.2);
	z-index: 4; /* Below slide menu */
	display: none;
}
