.detailContainer {
	margin-top: 10px;
	border: 1px solid #d6e9c6;
	border-radius: 4px;
}

.panelHead {
	padding: 10px;
	background: #dff0d8;
	display: flex;
	justify-content: space-between;
}

.btnAction {
	color: #fff;
	/* background-color: #5cb85c; */
	padding: 5px;
	margin-right: 10px;
	border-radius: 4px;
	display: inline-block;
}

.headPath {
	background-color: #f5f5f5;
	padding: 5px;
	border-radius: 4px;
	display: inline-block;
	user-select: text;
}

.dataView {
	background-color: #f5f5f5;
	border-radius: 4px;
	overflow: auto;
	padding: 0 20px;
}

.dataTitle {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 10px 5px;
}
