.collapsibleView {
	border-radius: 4px;
	border: 1px solid lightgray;
	background-color: white;
	margin: 5px;
}

.groupView {
	display: flex;
	align-items: center;
	justify-content: space-between;

	padding: 10px;
	background-color: lightgray;
}

.lblName {
	font-size: 13px;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}

.line-clamp {
	display: -webkit-box;
	-webkit-line-clamp: 3;
	-webkit-box-orient: vertical;
	overflow: hidden;
}

.itemView {
	display: flex;
	align-items: center;
	justify-content: space-between;

	padding: 10px;
	border-top: 1px solid lightgray;
}

.itemView:hover {
	background-color: #f2f2f2;
}

.getIcon {
	color: darkcyan;
}

.postIcon {
	color: orange;
}

.putIcon {
	color: dodgerblue;
}

.deleteIcon {
	color: orangered;
}
